<script>
export default {
  name: "CertificateNotFound",
  methods: {
    switchLang() {
      const currentPath = this.$route.path;
      const switchLangTo = this.$vuetify.lang.current === "ru" ? "en" : "ru";

      this.$vuetify.lang.current = switchLangTo;

      this.$router.replace({
        path: currentPath,
        query: {
          ...this.$route.query,
          lang: switchLangTo,
        },
      });
    },
  },
};
</script>
